import React from 'react'
import styled from 'styled-components'
import { Tag, Flex, Heading, Image } from '@pancakeswap-libs/uikit'
import { CommunityTag, CoreTag, NoFeeTag, RiskTag } from 'components/Tags'

export interface ExpandableSectionProps {
  lpLabel?: string
  multiplier?: string
  risk?: number
  depositFee?: number
  tokenSymbol?: string
  quoteTokenSymbol?: string
  isTokenOnly?: boolean
}

const Wrapper = styled(Flex)`
  svg {
    margin-right: 0.25rem;
  }
`

const MultiplierTag = styled(Tag)`
  margin-left: 4px;
`

const CardHeading: React.FC<ExpandableSectionProps> = ({
  lpLabel,
  multiplier,
  risk,
  tokenSymbol,
  quoteTokenSymbol,
  isTokenOnly,
  depositFee,
}) => {
  return (
    <Wrapper justifyContent="space-between" alignItems="center" mb="12px">
      {/* <Image src={`/images/farms/${farmImage}.png`} alt={tokenSymbol} width={64} height={64} /> */}
      <div style={{ width: 60, height: 60, position: 'relative' }} >
        <Image src={`/images/tokens/${tokenSymbol}.png`} alt={tokenSymbol} width={isTokenOnly ? 60 : 40} height={isTokenOnly ? 60 : 40} />
        {!isTokenOnly && <div style={{ position: 'absolute', width: 40, left: 20, top: 20 }} >
          <Image src={`/images/tokens/${quoteTokenSymbol}.png`} alt={quoteTokenSymbol} width={40} height={40} />
        </div>}
      </div>
      <Flex flexDirection="column" alignItems="flex-end">
        <Heading mb="4px">{lpLabel}</Heading>
        <Flex justifyContent="center">
          {depositFee === 0 ? <NoFeeTag /> : null}
          {/* {isCommunityFarm ? <CommunityTag /> : <CoreTag />} */}
          {/* <RiskTag risk={risk} /> */}
          <MultiplierTag variant="primary" outline>{multiplier}</MultiplierTag>
        </Flex>
      </Flex>
    </Wrapper>
  )
}

export default CardHeading
